.grt {
	font-size: 22px;
	line-height: 39px;
	padding: 0 24px;
}
.grt h1 {
	font-size: 45px;
	line-height: 72px;
	margin-bottom: 39px;
}
.grt .title {
	font-size: 58px;
	line-height: 90px;
	margin-bottom: 39px;
}
.grt h2 {
	font-size: 36px;
	line-height: 59px;
	margin-top: 63px;
	margin-bottom: 24px;
}
.grt h3 {
	font-size: 28px;
	line-height: 47px;
	margin-top: 39px;
	margin-bottom: 15px;
}
.grt h4 {
	font-size: 22px;
	line-height: 39px;
	font-weight: bold;
	margin-bottom: 9px;
}
.grt p, .grt ul, .grt ol, .grt blockquote, .grt pre, .grt .alert, .grt .note, .grt .box, .grt .footnotes {
	margin-bottom: 39px;
}
.grt ul, .grt ol {
	margin-left: 39px;
}
.grt li {
	margin-bottom: 15px;
}
.grt blockquote {
	padding-left: 24px;
	border-style: solid;
	border-color: rgba(0,0,0,0.15);
	border-width: 0 0 0 9px;
}
.grt sub, .grt sup {
	padding: 0 1px;
}
.grt code, .grt pre, .grt kbd {
	font-size: 20px;
}
.grt code {
	background-color: rgba(0,0,0,0.08);
	padding: 5px 6px;
	border-radius: 9px;
	margin: 0 1px;
}
.grt pre {
	padding: 15px;
	border-style: solid;
	border-color: rgba(0,0,0,0.15);
	border-width: 0 0 0 9px;
}
.grt kbd {
	background-color: #fff;
	padding: 5px 6px;
	border-radius: 6px;
	box-shadow: 0 0 6px 0 rgba(0,0,0,0.45);
	margin: 0 1px;
}
.grt .alert, .grt .note, .grt .box {
	padding: 24px;
}
.grt .footnotes {
	font-size: 17px;
	line-height: 31px;
	padding-top: 24px;
	border-top: 1px dotted rgba(0,0,0,0.15);
}
.grt .footnotes p {
	margin-bottom: 24px;
}
.grt ul ul, .grt ul ol, .grt ol ul, .grt ol ol, .grt blockquote.right p, .grt blockquote.left p, .grt .alert :last-child, .grt .note :last-child, .grt .box :last-child, .grt .footnotes :last-child {
	margin-bottom: 0;
}
@media all and (min-width: 1070px) {
	.grt {
		padding: 0 39px;
	}
}
